@import '../../global.scss';

.search-form {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    #search-input {
        width: 80%;
        padding: 15px 10px;
    }
    #submit-btn {
        margin-left: 10px;
        cursor: pointer;
        width: 20%;
        padding: 15px 5px;
    }
}

.error-container {
    color: white;
    position: absolute;
    top: 95px;
    animation: text-fade-in 0.3s ease-in;
}

h1 {
    color: #ffffff;
    font-size: 40px;
    margin: 20px 0;
    font-weight: 600;
    @include phone {
        font-size: 55px;
    }
    @include tablet {
        font-size: 72px;
    }
}

.previous-weather-container {
    width: 100%;

    p {
        color: #ffffff;
        font-size: 24px;
    }

    ul {

        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        li {
            margin: 15px 0;
            .previous-day-weather {
                font-size: 22px;
                padding: 0 12px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                height: 65px;
                background-color: #ffffff;
                .date-container{
                    display: flex;
                    justify-content: space-between;
                    width: 20%;
                    text-align: center;
                    span {
                        width: 50%;
                        text-align: start;
                    }
                }
            }
        }
    }
}